@import url(https://fonts.googleapis.com/css?family=Noto+Sans|Noto+Sans+KR);
.home-wrap{height:100vh;width:100vw}.home-wrap button{position:fixed;bottom:50px;bottom:calc(env(safe-area-inset-bottom)/2 + 50px);left:50%;-webkit-transform:translateX(-50%) translateY(100px);transform:translateX(-50%) translateY(100px);box-shadow:3px 3px 20px 5px rgba(0,0,0,.2);opacity:0;-webkit-animation:.3s 2s buttonMotion ease-out forwards;animation:.3s 2s buttonMotion ease-out forwards}@-webkit-keyframes buttonMotion{0%{opacity:0;-webkit-transform:translateX(-50%) translateY(100px);transform:translateX(-50%) translateY(100px)}100%{opacity:1;-webkit-transform:translateX(-50%) translateY(0);transform:translateX(-50%) translateY(0)}}@keyframes buttonMotion{0%{opacity:0;-webkit-transform:translateX(-50%) translateY(100px);transform:translateX(-50%) translateY(100px)}100%{opacity:1;-webkit-transform:translateX(-50%) translateY(0);transform:translateX(-50%) translateY(0)}}
.page-template{position:relative;display:flex;flex-direction:column;height:100%}
#drawingCanvas{width:100%;height:100%;background-color:#fff;border-radius:6px;aspect-ratio:4/3}#drawingCanvas.pen:hover{cursor:url(/picasso/static/media/brush.9d6cbdf8.svg) 0 30,pointer}#drawingCanvas.eraser:hover{cursor:url(/picasso/static/media/eraser.a64e50ad.svg) 0 30,pointer}
.chat-list-wrap{position:relative}.chat-list-wrap ul{position:relative;padding:10px 10px 20px 10px;height:100%;overflow-y:auto}.chat-list-wrap::after{content:"";width:100%;height:20px;position:absolute;bottom:0;left:0;background:linear-gradient(to top, var(--chakra-colors-gray-300), rgba(226, 232, 240, 0))}.chat-list-wrap ul{margin-bottom:20px;list-style:none}.chat-list-wrap ul li{width:100%;margin-bottom:10px}.chat-list-wrap .nickname{width:80%;margin-bottom:2px;font-size:12px;text-overflow:ellipsis;overflow:hidden;white-space:nowrap}.chat-list-wrap .body{display:inline-block;white-space:pre-line;word-break:break-all;padding:5px 10px;background-color:#fff;border-radius:5px;font-size:14px}.chat-list-wrap .mine{position:relative}.chat-list-wrap .mine::after{clear:both !important;display:block;content:""}.chat-list-wrap .mine::before{content:"";width:10px;height:10px;position:absolute;bottom:0;right:0;bottom:-5px;border-top:10px solid var(--chakra-colors-yellow-300);border-left:10px solid rgba(0,0,0,0)}.chat-list-wrap .mine .nickname{display:none}.chat-list-wrap .mine .body{float:right;background:var(--chakra-colors-yellow-300)}.chat-input-wrap textarea{min-height:100%;font-size:16px}.chat-input-wrap button{height:100%}
.dev-log{position:fixed;left:0px;bottom:0px;min-width:300px;min-height:100px;padding:10px;background:#333;color:#fff;border-radius:3px;border:solid 1px rgba(0,0,0,.3);z-index:1000}.dev-log ul{margin-left:20px}.dev-log ul strong{display:inline-block;width:100px}
.game-round{min-width:120px}
.game-timer{position:relative;min-width:120px;height:40px;display:inline-flex;justify-content:left;align-items:center}.game-timer .timer-emoji{font-size:30px;margin-right:10px}
.palette-card{position:relative;display:inline-block;margin:2px 2px 0 2px;width:22px;height:22px;border-radius:3px;cursor:pointer;border:solid 2px rgba(255,255,255,0)}.palette-card.selected{border-color:#515cff}.palette-card .icon-checked{display:inline-flex;align-items:center;justify-content:center;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}
.profile-avatar{display:inline-flex;align-items:center;justify-content:center;padding:0 2px;width:45px;height:100%;font-size:45px;line-height:1}
.result-score li{position:relative;display:flex;justify-content:space-between}.result-score li+li{margin-top:16px}.result-score li strong{margin:0 10px}.result-score li span{display:inline-flex;align-items:center;justify-content:center}.result-score li .rank-name{display:flex;align-items:center}.result-score li .medal{min-width:25px;font-size:25px;line-height:1;-webkit-transform:translate(-5px, -2px);transform:translate(-5px, -2px)}.result-score li .avatar-wrap{position:relative}.result-score li .avatar-wrap .crown{position:absolute;top:-12px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);font-size:18px;line-height:1}



.game-content-container{position:relative;width:100%;height:100%;border-radius:6px;overflow:hidden}.game-content-container .header{position:absolute;padding:10px;width:100%;top:0;left:0;pointer-events:none}.game-content-container .body{height:100%;z-index:100}.game-content-container .overlay-wrap{position:absolute;width:100%;height:100%;z-index:110;background:rgba(0,0,0,.4)}
.gate-container{display:flex;justify-content:center;align-items:center;height:100vh}.gate-container .chakra-form-control{width:auto}@media(max-width: 600px){.gate-container{justify-content:flex-start}}
.header-container h2{font-family:"Segoe UI",Tahoma,Geneva,Verdana,sans-serif}
.tool-type-wrap button{width:60px;height:40px;font-size:0;border:solid 1px rgba(0,0,0,.3);background-color:rgba(0,0,0,.1)}.tool-type-wrap button::after{content:"";width:100%;height:100%;background-color:#bbb}.tool-type-wrap button:hover{background-color:rgba(0,0,0,.15)}.tool-type-wrap button:hover::after{background-color:rgba(0,0,0,.15)}.tool-type-wrap button[data-active]{background-color:#4299e1}.tool-type-wrap button[data-active]::after{background-color:#fff;zoom:1.1}.tool-type-wrap .PEN::after{-webkit-mask:url(/picasso/static/media/brush.9d6cbdf8.svg) no-repeat 50% 50%;mask:url(/picasso/static/media/brush.9d6cbdf8.svg) no-repeat 50% 50%}.tool-type-wrap .ERASER::after{-webkit-mask:url(/picasso/static/media/eraser.a64e50ad.svg) no-repeat 50% 50%;mask:url(/picasso/static/media/eraser.a64e50ad.svg) no-repeat 50% 50%}.tool-type-wrap .CLEAR_ALL::after{-webkit-mask:url(/picasso/static/media/trash-can.fac72743.svg) no-repeat 50% 50%;mask:url(/picasso/static/media/trash-can.fac72743.svg) no-repeat 50% 50%;-webkit-mask-size:80%;mask-size:80%}
.user-list{height:100%;overflow-y:auto;list-style:none;padding:10px}.user-list li{position:relative}.user-list li+li{margin-top:10px}.user-list .profile-wrap{position:relative;padding-left:60px;margin:0;border:solid 2px rgba(0,0,0,0)}.user-list .profile-wrap.painter{border-color:var(--chakra-colors-pink-300)}.user-list .profile-wrap.painter .profile-avatar{zoom:1.5;left:4px;-webkit-animation:avatarShake .4s infinite linear alternate;animation:avatarShake .4s infinite linear alternate}@-webkit-keyframes avatarShake{0%{-webkit-transform:rotate(-8deg);transform:rotate(-8deg)}0%{-webkit-transform:rotate(8deg);transform:rotate(8deg)}}@keyframes avatarShake{0%{-webkit-transform:rotate(-8deg);transform:rotate(-8deg)}0%{-webkit-transform:rotate(8deg);transform:rotate(8deg)}}.user-list .profile-wrap .profile-avatar{position:absolute;top:0;left:12px}.user-list .profile-wrap .point{display:inline-block;width:100%;text-align:right}.user-list .nickname{text-overflow:ellipsis;overflow:hidden;white-space:nowrap}
#room{display:flex;justify-content:center;align-items:center;min-width:1200px}#room .joined{background-color:#286381}#room .joined>div{min-height:500px}#room .gate-container{height:100vh;background-color:#fff}.head-section{min-height:50px}.head-section h2{display:flex;align-items:center;font-family:"Segoe UI",Tahoma,Geneva,Verdana,sans-serif}
*{margin:0;padding:0;box-sizing:border-box;border:0;font-family:system-ui,"AppleSDGothicNeo",sans-serif;font-style:normal;word-wrap:break-word;word-break:keep-all;-webkit-hyphens:auto;hyphens:auto}*:focus{outline:none}input:disabled{background-color:#ebebe4}button{cursor:pointer;border:0;background:none}iframe{display:none !important}body{line-height:1.6;font-size:16px;font-family:"Noto Sans KR","Noto Sans","Malgun Gothic",sans-serif;color:#222;background:#1a202c}
