.dev-log {
  position: fixed;
  left: 0px;
  bottom: 0px;
  min-width: 300px;
  min-height: 100px;
  padding: 10px;
  background: #333333;
  color: #fff;
  border-radius: 3px;
  border: solid 1px rgba(0, 0, 0, 0.3);

  ul {
    margin-left: 20px;

    strong {
      display: inline-block;
      width: 100px;
    }
  }

  z-index: 1000;
}
